<template>
  <v-container class="bg-theme-smm" v-if="$route.params.id">
    <v-card>
      <div
        class="card-header mb-2 px-0 cursor-pointer"
        role="button"
        data-bs-toggle="collapse"
        data-bs-target="#kt_form1"
        aria-expanded="true"
        aria-controls="kt_form1"
      >
        <div class="card-title">
          <div class="d-flex align-items-center">
            <!--begin::Bullet-->
            <span
              class="bullet bullet-vertical h-50px"
              :class="`bg-yellow-smm`"
            ></span>
            <div class="ml-2 flex-grow-1">
              <h2 class="text-white text-hover-primary fw-bolder">
                Plan d'action {{ data.name }}
              </h2>
            </div>
          </div>
        </div>
      </div>
      <div id="kt_form1" class="pt-3 mx-4 collapse show">
        <div class="w-100">
          <div id="kt_account_profile_details container" class="collapse show">
            <div class="text-white fw-bold fs-2">
              <div class="d-flex flex-wrap">
                <v-form @submit.prevent="submitForm">
                  <v-row>
                    <v-col md="6" v-if="data.analyse_env">
                      <router-link
                        :to="
                          '/apps/analyse/show-analyse/' + data.analyse_env.id
                        "
                      >
                        <p class="bg-light bg-opacity-5 p-4 text-primary">
                          {{
                            "kt-" +
                            data.analyse_env.id +
                            " Impact Résiduel : " +
                            data.analyse_env.impact_residuel
                          }}
                        </p>
                      </router-link></v-col
                    >
                    <v-col md="6"
                      ><v-text-field v-model="data.name" label="Name" required>
                      </v-text-field
                    ></v-col>
                    <v-col md="6" class="d-none">
                      <v-text-field
                        v-model="data.ordre_priorite"
                        label="Priority"
                        type="number"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col md="6">
                      <v-text-field
                        v-model="data.delai"
                        label="Delai"
                        type="date"
                      >
                      </v-text-field>
                      <div class="bg-light bg-opacity-5 mb-3">
                        <v-label class="ml-3 my-4">Progrés</v-label>
                        <v-slider
                          step="5"
                          show-ticks
                          class="p-3"
                          v-model="data.progress"
                          :color="
                            data.progress < 30
                              ? 'info'
                              : data.progress < 60
                              ? 'primary'
                              : 'success'
                          "
                          thumb-label="always"
                        ></v-slider></div
                    ></v-col>

                    <v-col md="6">
                      <v-textarea
                        v-model="data.description"
                        label="Description"
                        required
                      >
                      </v-textarea>
                    </v-col>
                    <v-col md="6" class="d-none">
                      <v-textarea
                        v-model="data.notes"
                        label="Notes"
                      ></v-textarea
                    ></v-col>
                    <v-col md="6">
                      <v-file-input
                        v-model="data.documents"
                        label="Documents"
                      ></v-file-input
                    ></v-col>
                    <v-col md="6">
                      <v-select
                        v-model="data.responsables"
                        :items="userOptions"
                        :items-label="userOptions"
                        label="Responsables"
                        item-title="name"
                        return-object
                        multiple
                        chips
                      ></v-select>
                    </v-col>
                    <v-col md="6" class="d-none">
                      <v-text-field
                        v-model="data.cout"
                        label="COÙT"
                        type="number"
                      ></v-text-field
                    ></v-col>
                    <v-col md="12">
                      <v-btn type="submit" class="float-right btn-info btn m-4">
                        <span class="svg-icon svg-icon-3 text-white">
                          <inline-svg
                            src="media/icons/duotune/art/art005.svg"
                          />
                        </span>
                        Modifier</v-btn
                      >
                    </v-col></v-row
                  >
                </v-form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";
import listValRef from "@/core/data/valeur-ref";
import Swal from "sweetalert2/dist/sweetalert2.min.js";
import { setCurrentPageBreadcrumbs } from "@/core/helpers/breadcrumb";
import { onMounted } from "vue";
export default {
  setup() {
    onMounted(() => {
      setCurrentPageBreadcrumbs("Modifier un PAE", ["Plan d'action"]);
    });

    return {};
  },
  computed: {
    currentRouteName() {
      return window.location.origin;
    },
  },
  data() {
    return {
      data: {
        analyse_env: {},
        name: "",
        description: "",
        progress: 0,
        delai: null,
        ordre_priorite: 0,
        status: "planned",
        notes: "",
        documents: "",
        cout: null,
      },
      statusOptions: ["planned", "in progress", "completed"],
      userOptions: [],
      envOptions: [],
    };
  },
  async created() {
    this.fetch();
  },
  methods: {
    async fetch() {
      if (this.$route.params.id) {
        const analyse = await axios.get(
          this.currentRouteName + "/api/action/" + this.$route.params.id
        );
        this.data = analyse.data;
        const users = await axios.get(this.currentRouteName + "/api/users");
        this.userOptions = users.data;
      }
    },
    submitForm() {
      const data = {
        name: this.data.name,
        description: this.data.description,
        progress: this.data.progress,
        delai: this.data.delai,
        notes: this.data.notes,
        // documents: this.data.documents,
        cout: this.data.cout,
        responsables: this.data.responsables.map((user) => user.id),
      };
      axios
        .put(
          this.currentRouteName + "/api/action/" + this.$route.params.id,
          data
        )
        .then((response) => {
          Swal.fire({
            text: "Plan d'action bien Ajouté",
            icon: "success",
            buttonsStyling: false,
            confirmButtonText: "Ok",
            customClass: {
              confirmButton: "btn fw-bold btn-light-success",
            },
          }).then((data) => {
            this.$router.push({
              name: "apps-analyse-list-action",
            });
          });
        })
        .catch(function (error) {
          Swal.fire({
            text: error,
            icon: "error",
            buttonsStyling: false,
            confirmButtonText: "Réessayer!",
            customClass: {
              confirmButton: "btn fw-bold btn-light-danger",
            },
          });
        });
    },
  },
};
</script>
<style>
.bg-theme-smm {
  background: transparent;
}

